// App.js
import React, { useState } from 'react';
import SearchBar from './SearchBar';
import StockInfo from './StockInfo';
import StockGraph from './StockGraph'; //   
import axios from 'axios';
import convert from 'xml-js';
import styled from 'styled-components';

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Title = styled.h1`
  background-color: #0af07a;
  color: #000000;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
`;

function App() {
  const [stockData, setStockData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [graphData, setGraphData] = useState([]); //     

  const fetchStockData = async (itmsNm, startDate, endDate) => {
    setLoading(true);
    setError(null);
    setStockData(null);
    setGraphData([]);

    const API_KEY = 'CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD+chVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ=='; 
    const API_URL = `https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo`;

    try {
      let date = new Date(startDate);
      const endDateObj = new Date(endDate);
      const stockDataList = [];

      while (date <= endDateObj) {
        const formattedDate = date.toISOString().split('T')[0].replace(/-/g, '');
        console.log(` : ${formattedDate}`); 

        const response = await axios.get(API_URL, {
          params: { 
            serviceKey: API_KEY,
            itmsNm: itmsNm,
            basDt: formattedDate
          }
        });

        console.log('API :', response.data); 

        const jsonResult = convert.xml2js(response.data, { compact: true, spaces: 2 });
        console.log(' JSON:', jsonResult); // : JSON   

        const items = jsonResult.response.body.items?.item;

        if (items) {
          const itemArray = Array.isArray(items) ? items : [items];
          const stockItem = itemArray.find(item => item.itmsNm._text.toLowerCase() === itmsNm.toLowerCase());

          if (stockItem) {
            console.log(stockItem);
            stockDataList.push({
              date: formattedDate,
              itmsNm: stockItem.itmsNm?._text || '', //    
            mrktCtg: stockItem.mrktCtg?._text || '', //    
            clpr: parseFloat(stockItem.clpr?._text.replace(/,/g, '')) || 0, //     
            fltRt: parseFloat(stockItem.fltRt?._text) || 0,
            basDt: formattedDate
            }
          );
          }
        }

        date.setDate(date.getDate() + 1); //   
      }

      if (stockDataList.length > 0) {
        console.log('  :', stockDataList); // :    
        setGraphData(stockDataList); //   
        setStockData(stockDataList[stockDataList.length - 1]); //    
      } else {
        console.log('      .'); // :   
        setGraphData([]);
        setStockData(null);
        setError('      .');
      }
    } catch (error) {
      console.error('API  :', error); // : API   
      setError('  .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <AppContainer>
      <div className="App">
        <Title>   </Title>
        <SearchBar onSearch={fetchStockData} />
        {loading && <p> ...</p>}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        {stockData && <StockInfo data={stockData} />}
        {graphData.length > 0 && <StockGraph data={graphData} />} {/*      */}
      </div>
    </AppContainer>
  );
}

export default App;
